import { handleActions, createAction } from 'redux-actions';
import axios from 'axios';
import { fromJS } from 'immutable';

const getPostApi = postId => {
	return axios.get(`https://jsonplaceholder.typicode.com/posts/${postId}`);
};

const GET_POST_PENDING = '/lesson15/reduxThunk/GET_POST_PENDING';
const GET_POST_SUCCESS = '/lesson15/reduxThunk/GET_POST_SUCCESS';
const GET_POST_FAILURE = '/lesson15/reduxThunk/GET_POST_FAILURE';
const SET_INCREMENT = 'lesson15/reduxThunk/SET_INCREMENT';
const SET_DECREMENT = 'lesson15/reduxThunk/SET_DECREMENT';

const getPostPending = createAction(GET_POST_PENDING);
const getPostSuccess = createAction(GET_POST_SUCCESS);
const getPostFailure = createAction(GET_POST_FAILURE);
export const increment = createAction(SET_INCREMENT);
export const decrement = createAction(SET_DECREMENT);

// redux-thunk   
export const getPost = postId => dispatch => {
	//    - api 
	dispatch(getPostPending());

	//   - promise  
	return getPostApi(postId)
		.then(response => {
			//  GET_POST_SUCCESS  payload 
			dispatch(getPostSuccess(response));

			return response;
		})
		.catch(error => {
			//   GET_POST_FAILURE  payload 
			dispatch(getPostFailure(error));

			//   throw    catch    .
			throw error;
		});
};

// state 
const initialState = fromJS({
	num: 1,
	pending: false,
	error: false,
	data: {
		title: '',
		body: ''
	}
});

export default handleActions(
	{
		[SET_INCREMENT]: (state, action) => {
			return state.merge({ num: state.get('num') + 1 });
		},
		[SET_DECREMENT]: (state, action) => {
			return state.merge({ num: state.get('num') - 1 });
		},
		[GET_POST_PENDING]: (state, action) => {
			return state.merge({ pending: true, error: false });
		},
		[GET_POST_SUCCESS]: (state, action) => {
			return state.merge({ pending: false, data: action.payload.data });
		},
		[GET_POST_FAILURE]: (state, action) => {
			return state.merge({ pending: false, error: false });
		}
	},
	initialState
);



