import React, { Component } from 'react';
import PropTypes from 'prop-types';

class HelloWorld extends Component {
    static defaultProps = {
        name: ''
    }

    static propTypes = {
        name: PropTypes.string,
        age: PropTypes.number.isRequired
    }

    render() {
        return (
            <div>
                <p>.  { this.props.name } .</p>
                <p> { this.props.age } .</p>
            </div>
        );
    }
}

export default HelloWorld;


